import React from "react";
import {  Route, Switch,HashRouter} from "react-router-dom";

import MarketPlaceHome from './MarketPlaceHome';
import MyRunes from './MyRunes';
import ClaimedRunes from './ClaimedRunes';
import RuneDetail from './RuneDetail';

class Routes extends React.Component {
  render() {
    return (
      <HashRouter>
      <Switch>
        <Route exact  path="/" component={MarketPlaceHome} />
        <Route  path="/rune/:runeId" component={RuneDetail} />
        <Route   path="/myRunes" component={MyRunes} />
        <Route   path="/claimed" component={ClaimedRunes} />

      <Route
        render={function() {
          return <h1>Not Found</h1>;
        }}
      />
    </Switch></HashRouter>
    );
  }
}

export default Routes;
