import React from 'react';

import RuneImageComponent from "./RuneImageComponent"
import { Link} from "react-router-dom";

class ClaimedRunesComponent extends React.Component {

  constructor(props) {
    super(props);
    this.state={
      props
    }
    this.getRunesComponent = this.getRunesComponent.bind(this);
  }

  getRunesComponent(runesRemainingToAssign){
    let components = []
    let claimedRunes = 511- runesRemainingToAssign
    for(let counter=0;counter<claimedRunes;++counter){
        components.push(
            <Link to={"/rune/"+counter} style={{display:"table",float:"left",margin:"15px"}}>
              <RuneImageComponent isDetail={false}  runeId={counter} />
              <br />
               Rune #{counter}
            </Link>
        )
    }

    return components
  }

  render() {
    if (this.props.runesRemainingToAssign >=0) {
      return (
        <div    >
          <section >
            <div >
                Claimed runes ({511 - this.props.runesRemainingToAssign})
                
                <div style={{margin:"auto",width:"500px"}}>
                {this.getRunesComponent(this.props.runesRemainingToAssign)}
                </div>
            </div>
          </section>
        </div>
      );
    } else{
      return <> No Runes were claimed</>
    }
    
    return null;

  }
}

export default ClaimedRunesComponent;
