let ISPROD = true
class RuneImageComponent extends React.Component {

  constructor(props) {
    super(props);
    this.state={
      props
    }
    this.getRuneImage = this.getRuneImage.bind(this);
  }

  getRuneImage(rune){

    let runeId = Number.parseInt(rune)
    let colorString = ""
    let height ="50"
    let width = "auto"
    let imageEdges = " image-rendering: pixelated;object-fit: contain;"

    let divWidth = "90px"
    if(this.props.divWidth != undefined){
      divWidth = this.props.divWidth
    }

    if(this.props.height != undefined &&
      this.props.width != undefined){
        height = this.props.height 
        width = this.props.height 
    }

    if(!this.props.isDetail){
      imageEdges = ""
    }
    
  

    if (window.location.href.indexOf("localhost") != -1){
      ISPROD = false
    }
    
    let chosenModel;
    if(runeId<100){
      chosenModel = nft1
      colorString = COLOR_ARRAY[runeId]
      if(ISPROD){
        chosenModel = "/demo/images/nfts/1.png"
      }
    }
    else if(runeId>=100 && runeId<200){
      chosenModel = nft2
      colorString = COLOR_ARRAY[runeId-100]
      if(ISPROD){
        chosenModel = "/demo/images/nfts/2.png"
      }
    }else if(runeId>=200 && runeId<300){
      chosenModel = nft3
      colorString = COLOR_ARRAY[runeId-200]
      if(ISPROD){
        chosenModel = "/demo/images/nfts/3.png"
      }
    }
    else if(runeId>=300 && runeId<400){
      chosenModel = nft4
      colorString = COLOR_ARRAY[runeId-300]
      if(ISPROD){
        chosenModel = "/demo/images/nfts/4.png"
      }
    }
    else if(runeId>=400 && runeId<512){
      chosenModel = nft5
      colorString = COLOR_ARRAY[runeId-400]
      if(ISPROD){
        chosenModel = "/demo/images/nfts/5.png"
      }
    }


    let filterString = 'filter:hue-rotate('+colorString+'deg);'
    if(runeId>500){
      let temp = 600 - runeId
      let temp2 = temp - 13
      temp = temp *13
      filterString = 'filter:hue-rotate('+COLOR_ARRAY[temp2]+'deg) invert('+temp+'%);'
    }

    function createMarkup() { return {__html: '<style> .nft'+runeId+'{'+filterString+'image-rendering: auto;image-rendering: crisp-edges;'+imageEdges+' }</style>'}; };

    return(
      <div style={{width:divWidth,margin:"auto"}} >
        <div dangerouslySetInnerHTML={createMarkup()} />
        <img  className={"nft"+runeId} src={chosenModel} height={height} width={width} />
      </div>
    ) 
  }


  render() {
    let runeId = Number.parseInt(this.props.runeId)

    if (runeId>=0) {
      return (
       this.getRuneImage(runeId)
      );
    } 
    
    return null;

  }
}

export default RuneImageComponent;
