import React from 'react';
import { Link} from "react-router-dom";
import RuneImageComponent from "./RuneImageComponent"

class MyRunesComponent extends React.Component {

  constructor(props) {
    super(props);
    this.state={
      props
    }
    this.getRunesComponent = this.getRunesComponent.bind(this);
  }

  getRunesComponent(myRunes){
    let components = []

    for(let counter=0;counter<myRunes.length;++counter){
        components.push(
            <Link to={"/rune/"+myRunes[counter]} style={{display:"table",float:"left",margin:"15px"}}>
              <RuneImageComponent isDetail={false}  runeId={myRunes[counter]} />
              <br />
               Rune #{myRunes[counter]}
            </Link>
        )
    }

    return components
  }

  render() {
    if (this.props.myRunes.length >0) {
      return (
        <div    >
          <section >
            <div >
                My runes ({this.props.myRunes.length})
                
                <div style={{margin:"auto",width:"500px"}}>
                {this.getRunesComponent(this.props.myRunes)}
                </div>
            </div>
          </section>
        </div>
      );
    } 
    
    return null;

  }
}

export default MyRunesComponent;
